import React from 'react';
import { Flex, HeadingText } from 'components/shared';
export default () => {
    return (
        <Flex dir="column" ali="stretch">
            <HeadingText center bottom="0">" If you see other's mistake more than your mistake,</HeadingText>
            <HeadingText center>You will see other's bugs more than your bugs "</HeadingText>
        </Flex>
    );
};



